@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
body{
    margin:auto;
    font-family: poppins;
}
#header{
    background-color:#c5330e;
    height:100px;
    width:100%
}

.logo{
    height:50px;
    width:50px;
}

a{
    text-decoration: none;
}

sup{
    font-size: 9px;
}

#heading{
    color:rgb(214, 21, 21);
    text-align: center;
    font-size: 25px;
    margin-top: 15px;
    font-weight: bold;
}

#social{
    display: inline-block;
    margin-left:60%;
    margin-top:-4%
}
#login{
    color:white;
    display: inline-block;
    margin-right:15px;
    text-decoration: none;
    font-size:15px;
}
#createaccount{
    color:white;
    display: inline-block;
    border:1px solid white;
    margin:2px;
    padding:3px;
    margin-right:65px;
    border-radius:2px;
    text-decoration: none;
    font-size:15px;
}

#logo_main{
    height:70px;
    width:70px;
    background-color: white;
    border-radius:50%;
    margin-left:80px;
    display: inline-block; 
    margin-top: 1%;
}

#main{
    margin-left: 5%;
    margin-top: 5%;
}

#tagline{
    color:rgb(58, 58, 148);
}

#filter{
    height:700px;
    width:20%;
    float: left;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    /*background-image:linear-gradient(rgb(228, 215, 192),white);*/
} 

#content{
    width:65%;
    float: right;
    margin-right: 6%;
    margin-top: 1%;

}
.list_content{
    width:100%;
    height:300px;
    margin-top: 8%;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    
}
.filter_option{
    color:rgb(58, 58, 148);
    font-size: 25px;
    font-weight: bold;
    margin-left: 10%;
}
.subhead , .text {
    margin-left:10%;
    color:rgb(16, 16, 66);
}

.subhead{
    color:rgb(58, 58, 148);
}

.fort , .graytext , .rate , .menu_cuisines{
    font-weight: bold;
}
.image_display{
    height:140px;
    width:150px;
    border-radius:20%;
    padding:10px;
    margin-left:5%;
    margin-top:3%;
}
.name_address{
display: inline-block;
margin-top:6%;
position: absolute;
margin-left: 2%;
width: 45%;
color:rgb(53, 51, 51);
}
.images{
    display:inline;
}
hr{
    width:90%;
}
.menu_cuisines{
    display:inline-block;
    margin-left:7%;
    margin-top: 20px;
}
.rate{
    display: inline-block;
    margin-left:3%;
}
#pagination{
    display: inline-block;
    margin-top:2%;
    margin-left: 25%;
}
#pagination > a {
    color:rgb(78, 71, 71);
    font-size:25px;
    border:1px solid gray;
    border-radius: 5px;
    padding:10px;
    margin:3px;
}
.checked{
    color:orangered;
}
.star_rating{
    display: inline-block;
    margin-left:60%;
    margin-top:-20%;
}
.rest_name{
    color:rgb(58, 58, 148);
    margin-top: -37px;
}

.star_rating{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 500;
}
/*media queries section*/
@media screen and (max-width:1600px){
    body{
        background-color:green;
    }
}

@media screen and (max-width:1400px){
    body{
        background-color: white;
    }
}

@media screen and (max-width:1200px){
    body{
        background-color: skyblue;
    }
    #social{
        margin-left: 70%;
    }
}

@media screen and (max-width:935px){
    body{
        background-color: rgb(216, 30, 123);
    }
    #social{
        margin-left: 60%;
    }
}

@media screen and (max-width:800px){
    body{
        background-color: rgb(204, 216, 30);
    }
}

@media screen and (max-width:710px){
    body{
        background-color: rgb(30, 126, 216);
    }
    #social{
        margin-left: 40%;
        margin-top:-8%
    }
    #header{
        height:80px;
    }
}

@media screen and (max-width:480px){
    #social{
        margin-left: 30%;
        margin-top:-10%
    }
    #logo_main{
        margin-left:10px;
    }
}
* {
    font-family: "poppins";
    margin: 0;
    padding: 0;
}
body{
    /*background-color: rgb(245, 234, 234);*/
    background-color: #fff;
}
.login, .logout, .signup {
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.56;
    letter-spacing: normal;
    text-align: left;
    color: #fff;
    background-color: transparent;
    padding: 12px 14px 11px 15px;
    transition-duration: 1s;
}
.login, .logout {
    border: none;
    margin-top: 5px;
}
.signup {
    border: 1px solid #fff;
    background-repeat: no-repeat;
    margin-top: 15px;
}
.background {
    background-image: url("https://b.zmtcdn.com/web_assets/81f3ff974d82520780078ba1cfbd453a1583259680.png"); 
    width: 100%;
    height: 450px;
    background-position: center;
    background-size: cover;
  }
  .homelogo {
    width: 9rem;
    height: 9rem;
    padding: 15px 25px 16px 24px;
    background-color: white;
    border-radius: 50%;
    font-size: 42px;
    color: red;
    position: relative;
    top: 17%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
  
  header {
    color: white;
    font-size: 34px;
    text-align: center;
    position: relative;
    top: 20%;
    text-align: center;
    font-weight: normal;
  }
  #header{
      height:92px;
  }
  .dropdown-block{
    position: relative;
    top: 22%; 
    text-align: center;
  }

  .home-dropdown {
    width: 257px;
    background-color: #fff;
    padding-left: 10px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    border: none;
}
select {
    word-wrap: normal;
}
.home-dropdown, .input-box {
    height: 55px;
    font-size: 14px;
    color: #8c96ab;
}
.input-box {
    padding-left: 35px;
    width: 487px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border: none;
    margin-left: -22px;
}

.search {
    left: 10px;
    color: #8c96ab;
    font-size: 14px;
}
.glyphicon {
    position: relative;
    top: 1px;
    display: inline-block;
    font-family: "Glyphicons Halflings";
    font-style: normal;
    font-weight: 400;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
/*my css extra*/
.head{
    margin-top: 50px;
    margin-bottom: 50px;
    color:#333333e3;
}
.thumbnail{
    border-radius: 30px;
    border: none;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.thumbnail h3{
    text-align: center;
}
.thumbnail img{
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    transition: 0.7s;
    width:350px;
    height:150px;
    overflow: hidden;
}
.thumbnail img{
    transform: scale(1.02);
}


/*media queries starts here*/
@media screen and (max-width: 768px) {
    input.input-box {
      margin-top: 5px;
      margin-left: -15px;
      width: 500px;
      border-radius: 10px;
    }
    select.home-dropdown {
      width: 500px;
      border-radius: 10px;
    }
    span.search {
      left: 10px;
    }
    div.background{
      height: 550px;
    }
  }

  @media screen and (max-width: 500px) {
  input.input-box {
    width: 400px;
  }
  select.home-dropdown {
    width: 400px;
  }
}

