

.item_box{
    height:160px;
    width:300px;
    margin: 2%;
    display: inline-block;
    border-radius: 20px;
    background-color: #bfe3e7;
    margin-left: 100px;
    box-shadow: 16px 14px 17px #745454;
}

.item_image{
    width:100%;
    height:120px;
    border-radius: 20px;
}
.item_name{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:#000;

}