body{
    margin:auto;
    font-family: poppins;
}
#header{
    background-color:#c5330e;
    height:100px;
    width:100%
}

.logo{
    height:50px;
    width:50px;
}

a{
    text-decoration: none;
}

sup{
    font-size: 9px;
}

#heading{
    color:rgb(214, 21, 21);
    text-align: center;
    font-size: 25px;
    margin-top: 15px;
    font-weight: bold;
}

#social{
    display: inline-block;
    margin-left:60%;
    margin-top:-4%
}
#login{
    color:white;
    display: inline-block;
    margin-right:15px;
    text-decoration: none;
    font-size:15px;
}
#createaccount{
    color:white;
    display: inline-block;
    border:1px solid white;
    margin:2px;
    padding:3px;
    margin-right:65px;
    border-radius:2px;
    text-decoration: none;
    font-size:15px;
}

#logo_main{
    height:70px;
    width:70px;
    background-color: white;
    border-radius:50%;
    margin-left:80px;
    display: inline-block; 
    margin-top: 1%;
}

#main{
    margin-left: 5%;
    margin-top: 5%;
}

#tagline{
    color:rgb(58, 58, 148);
}

#filter{
    height:700px;
    width:20%;
    float: left;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    /*background-image:linear-gradient(rgb(228, 215, 192),white);*/
} 

#content{
    width:65%;
    float: right;
    margin-right: 6%;
    margin-top: 1%;

}
.list_content{
    width:100%;
    height:300px;
    margin-top: 8%;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    
}
.filter_option{
    color:rgb(58, 58, 148);
    font-size: 25px;
    font-weight: bold;
    margin-left: 10%;
}
.subhead , .text {
    margin-left:10%;
    color:rgb(16, 16, 66);
}

.subhead{
    color:rgb(58, 58, 148);
}

.fort , .graytext , .rate , .menu_cuisines{
    font-weight: bold;
}
.image_display{
    height:140px;
    width:150px;
    border-radius:20%;
    padding:10px;
    margin-left:5%;
    margin-top:3%;
}
.name_address{
display: inline-block;
margin-top:6%;
position: absolute;
margin-left: 2%;
width: 45%;
color:rgb(53, 51, 51);
}
.images{
    display:inline;
}
hr{
    width:90%;
}
.menu_cuisines{
    display:inline-block;
    margin-left:7%;
    margin-top: 20px;
}
.rate{
    display: inline-block;
    margin-left:3%;
}
#pagination{
    display: inline-block;
    margin-top:2%;
    margin-left: 25%;
}
#pagination > a {
    color:rgb(78, 71, 71);
    font-size:25px;
    border:1px solid gray;
    border-radius: 5px;
    padding:10px;
    margin:3px;
}
.checked{
    color:orangered;
}
.star_rating{
    display: inline-block;
    margin-left:60%;
    margin-top:-20%;
}
.rest_name{
    color:rgb(58, 58, 148);
    margin-top: -37px;
}

.star_rating{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 500;
}
/*media queries section*/
@media screen and (max-width:1600px){
    body{
        background-color:green;
    }
}

@media screen and (max-width:1400px){
    body{
        background-color: white;
    }
}

@media screen and (max-width:1200px){
    body{
        background-color: skyblue;
    }
    #social{
        margin-left: 70%;
    }
}

@media screen and (max-width:935px){
    body{
        background-color: rgb(216, 30, 123);
    }
    #social{
        margin-left: 60%;
    }
}

@media screen and (max-width:800px){
    body{
        background-color: rgb(204, 216, 30);
    }
}

@media screen and (max-width:710px){
    body{
        background-color: rgb(30, 126, 216);
    }
    #social{
        margin-left: 40%;
        margin-top:-8%
    }
    #header{
        height:80px;
    }
}

@media screen and (max-width:480px){
    #social{
        margin-left: 30%;
        margin-top:-10%
    }
    #logo_main{
        margin-left:10px;
    }
}